
html {
  font-size: 10px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background:url(bg.jpg) no-repeat;
  color: #333;
}

p {
  font-size: 1.6rem;
}

small {
  font-size: 1.2rem;
}

.container {
  margin: 0 auto;
  max-width: 1000px;
  padding: 40px;
}

.title {
  font-size: 4.4rem;
  text-align: center;
  color: bisque;
}

/******************** Searchbox form *********/

.form {
  display: flex;
  justify-content: center;
  align-items: center;
}

.label {
  font-size: 2.2rem;
  margin-bottom: 0.2rem;
  text-transform: uppercase;
  color: beige;
}

.input {
  font-size: 1.6rem;
  padding: 0.5rem 2rem;
  line-height: 2.6rem;
  border-radius: 20px;
  border: 1px solid #ddd;
  margin-bottom: 1rem;
}

.button {
  background-color: rgba(1,1,1,1);
  color: white;
  padding: 1rem 2rem;
  border: 1px solid rgba(255,255,255);
  border-radius:20px;
  font-size: 1.4rem;
  cursor: pointer;
  transition: background-color 250ms;
}

.button:hover {
  background-color: rgba(4, 41, 75, 0.096);
}

@media (min-width:786px){
  .form {
    grid-template-columns: auto 1fr auto;
    grid-gap: 1rem;
    align-items: center;
  }
  .input {
    margin-bottom:0;
  }
}

/**********************Styling movie card **********************/
.card-list {
  margin-top: 4rem;
}
.card{
  padding: 2rem 4rem;
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgba(0,0,0,0.75);
  margin-bottom: 2rem;
  background-color: white;
}
.card--title {
  margin-bottom: 0.5rem;
  font-size: 3.2rem;
}
.card--image {
  margin: 0 auto; 
  display: block;
}
